.productdisplay {
    display: flex;
    margin: 20px 90px;
    padding: 20px 0;
}

.productdisplay-left {
    display: flex;
    gap: 17px;
}

.productdisplay-img-list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.productdisplay-img-list img {
    height: 163px;
}

.productdisplay-main-img {
    width: 586px;
    height: 700px;
}

.productdisplay-right{
    margin: 0 60px;
    flex-direction: column;
}

.productdisplay-right h1 {
    color: #3d3d3d;
    font-size: 24px;
    font-weight: 700;
}

.productdisplay-right-stars {
    display: flex;
    margin-top: 13px;
    align-items: center;
    gap: 5px;
    color: #1c1c1c;
    font-size: 1rem;
}

.productdisplay-right-prices {
    display: flex;
    margin: 30px 0;
    gap: 30px;
    font-size: 22px;
    font-weight: 700;
}

.productdisplay-right-price-old {
    color: #818181;
    text-decoration: line-through;
}

.productdisplay-right-price-new {
    color: #ff4141;
}

.productdisplay-right-size h1 {
    margin-top: 45px;
    color: #656565;
    font-size: 20px;
    font-weight: 600;
}

.productdisplay-right-sizes {
    display: flex;
    margin: 30px 0;
    gap: 20px;
}

.productdisplay-right-sizes div {
    padding: 18px 24px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;
}

.productdisplay-right button {
    padding: 20px 40px;
    width: 200px;
    font-size: 1rem;
    font-weight: 600;
    color: #fff;
    background: #ff4141;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;
}

.productdisplay-right-category {
    margin-top: 10px;
}

.productdisplay-right-category span {
    font-weight: 600;
}

/* Media Queries */
@media(max-width: 1280px) {
    .productdisplay {
        margin: 0 60px;
    }

    .productdisplay-left {
        gap: 10px;
    }

    .productdisplay-img-list {
        gap: 10px;
    }

    .productdisplay-img-list img {
        height: 120px;
    }

    .productdisplay-main-img {
        width: auto;
        height: 510px;
    }

    .productdisplay-right {
        margin: 0 30px;
    }

    .productdisplay-right h1 {
        font-size: 22px;
    }

    .productdisplay-right-stars {
        gap: 3px;
        font-size: 13px;
    }

    .productdisplay-right-prices {
        margin: 10px 0;
        font-size: 18px;
    }

    .productdisplay-right-description {
        font-size: 13px;
    }

    .productdisplay-right-size h1 {
        margin-top: 20px;
        font-size: 20px;
    }

    .productdisplay-right-sizes div {
        padding: 14px 20px;
    }

    .productdisplay-right button {
        width: 150px;
        padding: 15px 0;
        margin-bottom: 20px;
    }

    .productdisplay-right-category {
        margin-top: 5px;
        font-size: 14px;
    }
}

@media(max-width: 1024px) {
    .productdisplay {
        margin: 0 30px;
    }

    .productdisplay-left {
        gap: 5px;
    }

    .productdisplay-img-list img {
        height: 80px;
    }

    .productdisplay-main-img {
        height: 350px;
    }

    .productdisplay-right h1 {
        font-size: 18px;
    }

    .productdisplay-right-stars img {
        width: 15px;
    }

    .productdisplay-right-description {
        font-size: 12px;
    }

    .productdisplay-right-sizes {
        margin-top: 10px;
    }

    .productdisplay-right-sizes div {
        padding: 10px 16px;
        font-size: 12px;
    }

    .productdisplay-right button {
        width: 120px;
        padding: 10px 0;
        margin-bottom: 10px;
        font-size: 14px;
    }

    .productdisplay-right-category {
       font-size: 12px;
    }
}

@media(max-width: 800px) {
    .productdisplay {
        margin: 0 10px;
    }

    .productdisplay-left {
        gap: 0px;
        flex-direction: column-reverse;
        align-items: center;
    }

    .productdisplay-img-list {
        flex-direction: row;
        gap: 6px;
    }

    .productdisplay-img-list img {
        height: 70px;
    }

    .productdisplay-main-img {
        height: 300px;
    }

    .productdisplay-right h1 {
        font-size: 14px;
    }

    .productdisplay-right-stars img {
        width: 13px;
    }

    .productdisplay-right-description {
        font-size: 10px;
    }
    .productdisplay-right-size h1 {
        margin: 10px 0;
    }

    .productdisplay-right-sizes {
        margin-top: 10px;
        gap: 5px;
    }

    .productdisplay-right-sizes div {
        padding: 4px 11px;
    }

    .productdisplay-right button {
        width: 100px;
        font-size: 12px;
    }

    .productdisplay-right-category {
       font-size: 10px;
    }
}

@media(max-width: 500px) {
   .productdisplay {
        flex-direction: column; 
   }

   .productdisplay-left {
        gap: 10px;
        flex-direction: row;
        margin: auto;
   }

   .productdisplay-img-list {
        flex-direction: column;
        gap: 8px;
   }

   .productdisplay-img-list img {
        height: 75px;
   }

   .productdisplay-main-img {
    height: 330px;
   }

   .productdisplay-right {
        margin: 5px;    
   }

   .productdisplay-right h1 {
        margin-top: 15px;
        font-size: 18px;
        font-weight: 500;
   }

   .productdisplay-right-stars img {
        width: 15px;
   }

   .productdisplay-right-sizes {
        gap: 10px;
        margin: 20px 0;
   }

   .productdisplay-right-sizes div {
        padding: 10px 16px;
        font-size: 14px;
   }

   .productdisplay-right button {
        width: 130px;
        font-size: 15px;
        padding: 12px 0;
   }

   .productdisplay-right-category {
        font-size: 16px;
   }
}