.hero {
    height: 100vh;
    background: rgb(238,174,202);
    background: radial-gradient(circle, rgba(238,174,202,1) 28%, rgba(247,244,244,1) 100%);
    display: flex;
}

/* Hero left side */
.hero-left {
    height: 100%;
    flex: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    padding-left: 140px;
    line-height: 1.1;
}

.hero-left h2 {
    color: #090909;
    font-size: 26px;
    font-weight: 600;
}

.hero-left p {
    color: #171717;
    font-size: 80px;
    font-weight: 700;
}

.hero-hand-icon {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.hero-hand-icon div {
    display: flex;
    align-items: center;
}

.hero-hand-icon img {
    width: 105px;
}

.hero-latest-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: #fff;
    font-size: 22px;
    font-weight: 500;
}

/*  */
.hero-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.hero-right img {
    height: 100%;
}

.hero-hand-icon img {
    width: 120px;
} 

/* Media Queries */
@media(max-width: 1280px) {
    .hero-left {
        padding-left: 100px;
    }

    .hero-left h2 {
        font-size: 22px;
    }

    .hero-left p {
        font-size: 60px;
    }

    .hero-hand-icon img {
        width: 80px;
    }

    .hero-latest-btn {
        width: 220px;
        height: 50px;
        font-size: 16px;
    }

    .hero-right img {
        width: 400px;
    }
}

@media(max-width: 1024px) {
    .hero-left {
        padding-left: 80px;
    }

    .hero-left h2 {
        font-size: 20px;
    }

    .hero-left p {
        font-size: 50px;
    }

    .hero-hand-icon img {
        width: 65px;
    }

    .hero-latest-btn {
        gap: 10px;
        width: 250px;
        height: 60px;
        font-size: 18px;
    }

    .hero-right img {
        width: 500px;
    }
}

@media(max-width: 800px) {
    .hero-left {
        padding-left: 30px;
    }

    .hero-left h2 {
        font-size: 16px;
    }

    .hero-left p {
        font-size: 40px;
    }

    .hero-hand-icon img {
        width: 50px;
    }

    .hero-latest-btn {
        gap: 10px;
        width: 175px;
        height: 40px;
        font-size: 13px;
    }

    .hero-right img {
        width: 300px;
    }
}

@media(max-width: 500px) {
    .hero {
        flex-direction: column;
    }

    .hero-right {
        display: none;
    }

    .hero-left h2 {
        font-size: 18px;
    }

    .hero-left p {
        font-size: 50px;
    }

    .hero-hand-icon img {
        width: 55px;
    }

    .hero-latest-btn {
        width: 200px;
        height: 45px;
        font-size: 14px;
    }
}