.relatedproducts {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 50px;
}

.relatedproducts h1 {
    color: #171717;
    font-size: 40px;
    font-weight: 600;
}

.relatedproducts hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.relatedproducts-item {
    margin-top: 50px;
    display: flex;
    gap: 30px;
}

/* Media Queries */
@media(max-width: 1280px) {
    .relatedproducts {
        height: 70vh;
    }

    .relatedproducts h1 {
        font-size: 40px;
    }

    .relatedproducts-item {
        gap: 20px;
        margin-top: 30px;
    }
}

@media(max-width: 1024px) {
    .relatedproducts {
        height: 60vh;
    }

    .relatedproducts h1 {
        font-size: 30px;
    }

    .relatedproducts-item {
        gap: 15px;
        margin-top: 20px;
    }
}
@media(max-width: 800px) {
    .relatedproducts {
        height: 50vh;
        gap: 6px;
    }

    .relatedproducts h1 {
        font-size: 20px;
    }

    .relatedproducts-item {
        gap: 5px;
    }
}

@media(max-width: 500px) {
    .relatedproducts {
        height: 90%;
    }

    .relatedproducts-item {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }
}