.offers {
    width: 80vw;
    height: 86.5vh;
    display: flex;
    margin: auto;
    padding: 0 100px;
    margin-top: 150px;
    margin-bottom: 50px;
    background: rgb(238,174,202);
    background: radial-gradient(circle, rgba(238,174,202,1) 28%, rgba(247,244,244,1) 100%);
}

/* Offers left side */
.offers-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.offers-left h1 {
    color: #171717;
    font-size: 70px;
    font-weight: 600;
}

.offers-left p {
    color: #171717;
    font-size: 22px;
    font-weight: 600;

}

.offers-left button {
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: #fff;
    font-size: 22px;
    font-weight: 500;
    margin-top: 20px;
    cursor: pointer;
}

/* Offers right */
.offers-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

/* Media Queries */
@media(max-width: 1280px) {
    .offers {
        padding: 0 80px;
        margin-bottom: 120px;
    }

    .offers-left h1 {
        font-size: 40px;
    }

    .offers-left p {
        font-size: 18px;
    }
    .offers-right img {
        width: 352px;
    }

    .offers-left button {
        width: 220px;
        height: 50px;
        font-size: 18px;
    }

    .offers-right {
        padding-top: 30px;
    }
}

@media(max-width: 1024px) {
    .offers {
        padding: 0 60px;
        margin-bottom: 80px;
        height: 40vh;
    }

    .offers-left h1 {
        font-size: 30px;
    }

    .offers-left p {
        font-size: 16px;
    }

    .offers-right img {
        width: 166px;
    }

    .offers-left button {
        width: 160px;
        height: 40px;
        font-size: 16px;
    }

    .offers-right {
        padding-top: 10px;
    }
}

@media(max-width: 800px) {
    .offers {
        margin-bottom: 60px;
    }

    .offers-left h1 {
        font-size: 22px;
    }

    .offers-left p {
        font-size: 13px;
    }

    .offers-right img {
        width: 166px;
    }

    .offers-left button {
        width: 140px;
        height: 30px;
        font-size: 12px;
    }
}

@media(max-width: 500px) {
    .offers {
        height: 25vh;
        margin-bottom: 50px;
    }

    .offers-left h1 {
        font-size: 16px;
    }

    .offers-left p {
        font-size: 10px;
    }

    .offers-right img {
        width: 102px;
    }

    .offers-left button {
        width: 120px;
        height: 25px;
        font-size: 10px;
        margin-top: 12px;
    }
}